body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.board {
  margin: auto;
  margin-top: 15px;
  border-collapse: collapse;
}

.App {
  text-align: center;
}

button{
  margin: 20px;
  border-radius: 8px;
  box-shadow: 0 6px 8px 0 rgba(0,0,0,0.24), 0 10px 30px 0 rgba(0,0,0,0.19);
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 18vmin;
  }
  
  .App-header {
    background-color: #282c34;
    max-height: 25vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

  @keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

  @media screen and (max-width: 1040px){
    .App-logo{
      height: 15vmin;
    }
    .App-header{
      font-size: 30px;
    }
    select{
      font-size: 24px;
    }
  }

  @media screen and (max-width: 500px){
    .App-logo{
      height: 18vmin;
    }
    .App-header{
      font-size: 20px;
    }
    select{
      font-size: 15px;
    }
  }
.square {
  background: #fff;
  border: 2px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 40px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 50px;
}

.input-square {
  max-width: 80%;
  height: 80%;
  font-size: 24px;
  text-align: center;
  border: none;
  font-weight: 700;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.correct-input-square {
  background: #bdf3bd;
  color: #187318;
  max-width: 80%;
  height: 80%;
  font-size: 24px;
  text-align: center;
  border: none;
  font-weight: 700;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.incorrect-input-square {
  background: #ec5b5b;
  color: #187318;
  max-width: 80%;
  height: 80%;
  font-size: 24px;
  text-align: center;
  border: none;
  font-weight: 700;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

@media screen and (max-width: 1040px){
 .square {
   font-size: 36px;
   height: 60px;
   width: 75px;
 } 
}

@media screen and (max-width: 700px){
  .square {
    font-size: 25px;
    height: 34px;
    width: 36px;
  }
}
.board-row:nth-child(3) .square, .board-row:nth-child(6) .square{
    border-bottom: 2px solid black;
}

.board-row:nth-child(4) .square, .board-row:nth-child(7) .square{
    border-top: 2px solid black; 
}

.board-row .square:nth-child(3), .board-row .square:nth-child(6){
    border-right: 2px solid black;
}

.board-row .square:nth-child(4), .board-row .square:nth-child(7){
    border-left: 2px solid black;
}
    .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }

